import React, { useRef, useState, useEffect } from 'react';
import { Editor } from '@tinymce/tinymce-react';
import { Grid } from '@mantine/core';
import { Breadcrumbs, Anchor } from '@mantine/core';
import { ActionIcon, Box, NavLink, Flex, TextInput, FileInput, Button } from '@mantine/core';
import { IconFingerprint, IconGauge, IconPlus, IconFolder, IconDeviceFloppy, IconEdit, IconUpload } from '@tabler/icons-react';
import { v4 } from 'uuid';

const Nav = (props) => {
  const [itemInDoc, setItemInDoc] = useState({parent: -1, children: -1});
  const [active, setActive] = useState("");
  const [itemIsEdited, setItemIsEdited] = useState("");
  const [data, setData] = useState(props.data);
  const [visible, setVisible] = useState(props.data);

  useEffect(() => {
    setData(props.data)
    setVisible(props.isVisible)
  }, [props.data]);

  return (
    
    <Box>
      
      <Flex justify="flex-end" align="flex-end" direction="row" wrap="wrap" style={{background: ""}}>
        <ActionIcon onClick={() => {props.onNewItem(itemInDoc, false)}}> <IconPlus size="1.125rem" /> </ActionIcon>
        <ActionIcon onClick={() => {props.onNewItem(itemInDoc, true)}}> <IconFolder size="1.125rem" /> </ActionIcon>
      </Flex>
      {
        data.map((item, index) => (
          <NavLink
            key={item.uuid}
            active={item.uuid === active}
            label={item.isNew || itemIsEdited==item.uuid ? <TextInput onBlur={(event) => props.onEditItem(item.uuid, {parent: index, children: -1}, {label: event.currentTarget.value})} classNames={{ input: 'inputLabel' }} placeholder={item.label}></TextInput> : item.label}
            description={item.description}
            rightSection={item.rightSection}
            childrenOffset={28}
            onClick={() => {setActive(item.uuid); setItemInDoc({parent: index, children: -1}); }}
          >
            { item.children && item.children.map((child, ch_index) => (
              <NavLink
                key={child.uuid}
                active={child.uuid === active}
                label={child.isNew || itemIsEdited==child.uuid ? <TextInput onBlur={(event) => props.onEditItem(child.uuid, {parent: index, children: ch_index}, {label: event.currentTarget.value})} classNames={{ input: 'inputLabel' }} placeholder={child.label}></TextInput> : child.label}
                description={child.description}
                rightSection={child.rightSection}
                onClick={() => {setActive(child.uuid); setItemInDoc({parent: index, children: ch_index}); props.onSelectItem(child.uuid, {parent: index, children: ch_index}); }}
              >

              </NavLink>
            ))}
          </NavLink>
        ))
      }

    </Box>
  )
}

export default function App() {
  const editorRef = useRef(null);
  const [notes, setNotes] = useState({});
  const [data, setData] = useState([]);
  const [activeItem, setActiveItem] = useState("");
  const [title, setTitle] = useState("");
  const [breadcumbs, setBreadcumbs] = useState([]);
  const [file, setFile] = useState(null);
  const fileData = useRef({})

  useEffect(() => {
    fetch('./notes_name_longer.json', {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
      },
    })
    .then(response => response.json())
    .then(json_content => {
      console.log(JSON.stringify(json_content))
      fileData.current = json_content
      setNotes(fileData.current.data)
      setData(fileData.current.structure)
    })
  }, [])

  async function open(payload){
    setFile(payload)
    let fileReader;
  
    const handleFileRead = (e) => {
      const content = fileReader.result;
      let json_content = JSON.parse(content)
      fileData.current = json_content
      console.log(fileData.current)
      setNotes(fileData.current.data)
      setData(fileData.current.structure)
    };
    
    fileReader = new FileReader();
    fileReader.onloadend = handleFileRead;
    fileReader.readAsText(payload);
  }

  const onNavNewItem = function(indexes, folder){
    if(indexes){
      if(!folder && indexes && indexes.parent >=0){
        let t_data = [...data]
        let new_uuid = v4()
        if( !t_data[indexes.parent].hasOwnProperty('children') ) {
          t_data[indexes.parent].children = []
        }
        t_data[indexes.parent].children.push({ uuid: new_uuid, children: [], label: 'New Item', isNew: true})
        setData(t_data)
        let t_notes = {...notes}
        t_notes[new_uuid] = ""
        setNotes(t_notes)
      } else if(folder){
        let t_data = [...data]
        t_data.push({ uuid: v4(), children: [], label: 'New Item', isNew: true})
        setData(t_data)
      }
    }
  }

  const onNavSelect = function(uuid, indexes){
    if(activeItem != uuid){
      if(activeItem != ""){
        let content = editorRef.current.getContent()
        let t_notes = {...notes}
        t_notes[activeItem] = btoa(content)
        setNotes(t_notes)
      }
      setActiveItem(uuid)
      if(indexes && indexes.children != -1){
        editorRef.current.setContent(atob(notes[uuid]))
        setTitle(data[indexes.parent].children[indexes.children].label)
        let breadcumbs = [data[indexes.parent].label, data[indexes.parent].children[indexes.children].label]
        setBreadcumbs(breadcumbs)
      } 

    }
  }

  const onNavEditItem = function(uuid, indexes, update){
    if(indexes && indexes.children != undefined && indexes.parent != undefined){
      let t_data = [...data]
      if(indexes.children == -1){
        t_data[indexes.parent].label = update.label
        t_data[indexes.parent].isNew = false
      } else if(indexes.children >= 0){
        t_data[indexes.parent].children[indexes.children].label = update.label
        t_data[indexes.parent].children[indexes.children].isNew = false
        let breadcumbs = [data[indexes.parent].label, data[indexes.parent].children[indexes.children].label]
        setBreadcumbs(breadcumbs)
      }
      setData(t_data)
    }
  }

  const save = function(){
    window.webkit.messageHandlers.bridge.postMessage(JSON.stringify(data))
  }

  breadcumbs.map((item, index) => (
    <Anchor href={item.href} key={index}>
      {item.title}
    </Anchor>
  ));

  return (
    <div>
      <Grid style={{height: '100vh', background: "#f2f7fa"}}>
        <Grid.Col span={2} style={{background: "#f9fbfc"}}>
          <h2 style={{padding: "0 20px", marginBottom: "5px"}}>
            All notes
          </h2>
          <FileInput
                style={{padding: "0px", margin: "5px", width: "99%", background: "white", border: "1px dotted lightgrey"}}
                placeholder="Select a notes file to open"
                variant="unstyled"
                size="sm"
                value={file} onChange={open}
                icon={<IconUpload size="1.2rem"/>}
          />
          <Nav isVisible={file != null} onSelectItem={onNavSelect} onNewItem={onNavNewItem} onEditItem={onNavEditItem} data={data}></Nav>
        </Grid.Col>
        <Grid.Col span={10} style={{padding : "0", border: "1px solid lightgrey"}}>
          <Flex style={{margin: '10px 0px', padding: "5px", background: "white"}} justify="flex-start" align="flex-start" direction="row" wrap="wrap">
            <Button onClick={save} variant="subtle" leftIcon={<IconDeviceFloppy size="1.2rem" />} color="gray">Save</Button>
          </Flex>
          { file == null && <div>

          </div>}
          { file != null && <Box style={{width: "96%", padding: "5px 2%", margin: "2%", background: "white"}}>
            <Breadcrumbs separator="" mt="xs" style={{padding : "0px 0px"}}>
              { breadcumbs.length>0 && breadcumbs.map((item, index) => (
                  <span key={index}>{item}</span>
                ))
              }
            </Breadcrumbs>
            <h2>{title}</h2>
            <Editor
              tinymceScriptSrc={process.env.PUBLIC_URL + '/tinymce/tinymce.min.js'}
              onInit={(evt, editor) => {
                editorRef.current = editor
              }
              }
              initialValue='Sampmle text'
              init={ 
                {
                  menubar: false,
                  height: "95vh",
                  plugins: [
                    'advlist autolink lists link image charmap print preview anchor',
                    'searchreplace visualblocks code fullscreen',
                    'insertdatetime media table paste code help wordcount'
                  ],
                  toolbar: 'undo redo | formatselect | ' +
                  'bold italic backcolor | alignleft aligncenter ' +
                  'alignright alignjustify | bullist numlist outdent indent | ' +
                  'removeformat | help | mybutton',
                  branding: false,
                  statusbar: false,
                  extended_valid_elements : 'flashcard[*]',
                  custom_elements : '~flashcard',
                  content_css: "/flashcard.css",
                  setup: function (editor) {
                    editor.ui.registry.addButton('mybutton', {
                      text: 'My Button', 
                      onAction: function() { 
                        // Add your own code to execute something on click 
                        editor.focus();
                        editor.selection.setContent('<flashcard class="f">' + editor.selection.getContent() + '</flashcard>');
                        var resultingSource = editor.getContent()
                        console.log(resultingSource)
                      }
                    });
                  }
                }
              }
            />
          </Box> }
        </Grid.Col>
      </Grid>
    </div>
  );
}
